@import url(https://fonts.googleapis.com/css2?family=Assistant&display=swap);
@font-face {
  src: url(/static/media/kumb-sans.7473a8f7.ttf);
  font-family: "Kumbh Sans";
}

:root {
  --color-primary: #397f6e;
  --color-primary-transparent: rgba(57, 127, 110, 0.2);
  --color-text: #000000;
  --color-disabled: #d9d9d9;
  --color-grey: #b5b5b5;
  --color-lightgrey: #f6f7f9;
  --color-lightgreen: #ebf2f1;
  --color-white: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 17px;
}

body {
  font-family: "Assistant", sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 400;
  font-family: 'Kumbh Sans', sans-serif;
}

.hover {
  transition: box-shadow 0.1s ease-in-out;
  border-radius: .5rem;
}

.hover:hover {
  box-shadow: 0 0 0 0.33rem rgba(57, 127, 110, 0.2);
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}
header {
  height: 5em;
  padding: 1rem;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav,ul {
  height: 100%;
}

ul {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

ul > li > a {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul > li a {
  font-size: 17px;
  color: var(--color-text);
  text-decoration: none;
  padding: 0.5em;
}

nav > div {
  display: flex;
  grid-gap: .5rem;
  gap: .5rem;
  align-items: center;
}

nav > div > div {
  display: flex;
  align-items: center;
  grid-gap: .5rem;
  gap: .5rem;
}

.hamburger {
  width: 25px;
  height: 2px;
  background-color: var(--color-primary);
  order: 2;
  position: relative;
  display: none;
}

.hamburger::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  top: -7px;
  background-color: inherit
}

.hamburger::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  top: 7px;
  background-color: inherit
}

@media (max-width: 60rem) {
  .hamburger {
    display: block;
  }
  nav > ul > li:not(:first-child),
  nav > div {
    display: none;
  }
}
.btn {
  padding: 12.75px 25.5px;
  border-radius: 3.4px;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  font-size: 17px;
  display: inline-flex;
  transition: all .1s ease-in-out;
  background-color: #EFEFEF;
  cursor: pointer;
}

.btn.small {
  padding: 0.5rem 1rem;
}

.btn.full {
  background-color: var(--color-primary);
  color: white;
}

.btn:hover {
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}
.sidebar > h2 {
  margin-bottom: 1em;
}

label {
  display: block;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: .5rem;
  position: relative;
}

fieldset {
  border: none;
  background-color: var(--color-white);
  margin-bottom: 2rem;
  padding: 1rem;
  border-radius: 5px;
}

legend {
  display: flex;
  justify-content: center;
  align-items: center;
}

legend > img {
  margin-right: .5em;
}

input[type="radio"] {
  display: none;
}

label:hover .custom-checkbox {
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}

input[type="radio"]:checked + .custom-checkbox {
  background-color: var(--color-primary);
  border-width: 0;
}

input[type="radio"]:checked + .custom-checkbox::after {
  background-color: var(--color-white);
}

.custom-checkbox {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  border: 1px solid #bdbec0;
  margin-right: 0.4em;
  position: relative;
  transition: all .3s;
}

.custom-checkbox::after {
  content: "";
  position: absolute;
  width: 0.5em;
  height: 0.5em;
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

input[type=search] {
  border: none;
  border-bottom: 0.15rem solid #dddee0;
  padding: .5rem .5rem .35rem 2rem;
  border-radius: 0.2rem;
  width: 300px;
  outline: none;
  transition: all .3s;
  width: 100%;
  font-size: 1em;
  margin: 1em 0;
}

input[type=search]:hover {
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}

input[type=search]:focus {
  border-bottom-color: var(--color-primary);
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}

label > img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: .5em;
}

.full-article .sidebar form {
  display: none;
}

@media (max-width: 85rem) {
  .full-article .sidebar {
    display: none;
  }
}

@media (max-width: 85rem) {
  .sidebar {
    order: 1;
  }
  .sidebar > h2 {
    display: none;
  }
}

.recent-article {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 1em;
  cursor: pointer;
}

.recent-article img {
  height: 4rem;
  width: 4rem;
  object-fit: cover;
}

.recent-article h3 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 12rem;
  font-size: 1rem;
  font-weight: bold;
}

.recent-article section {
  display: flex;
  flex-direction: column;
  height: 4rem;
  justify-content: space-between;
  padding: 0.5em 0 0.5em 0.5em;
}

.recent-article section time {
  color: var(--color-grey);;
}

@media (max-width: 85rem) {
  .recent-article {
    display: none;
  }
}

article.single-article {
  height: 450px;
  width: 100%;
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 4rem;
  max-width: 1200px;
  opacity: 0;
  transform: translateY(25%);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

article.single-article.animate {
  opacity: 1;
  transform: translate(0);
}

article.single-article > img,
article.single-article > .body {
  width: 50%;
}

article.single-article > img {
  height: 100%;
  object-fit: cover;
}

article.single-article .body {
  padding: 1em 2em 1em 3em;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
}

article.single-article .body > p:first-child {
  color: var(--color-grey);
  font-size: 1rem;
  margin-bottom: 2em;
}

article.single-article .body > p {
  font-size: 21px;
  margin-bottom: auto;
}

article.single-article .body > h2 {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 1em;
}

@media (max-width: 60rem) {
  article.single-article {
    flex-direction: column;
    height: auto;
    max-width: 35rem;
  }

  article.single-article > img,
  article.single-article > .body {
    width: 100%;
  }

  article.single-article > img {
    height: 300px;
    object-fit: cover;
  }

  article.single-article .body > p {
    margin-bottom: 1em;
  }
}

@media (max-width: 35rem) {
  article.single-article .body {
    padding: 1em;
  }
}
.banner {
  width: 90%;
  height: 60vh;
  margin: 0 auto;
  background-color: var(--color-lightgreen);
  padding: 1rem;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 8rem;
}

.banner > div {
  max-width: 600px;
  text-align: center;
}

.banner h1 {
  font-size: 58px;
  line-height: 1.15em;
  letter-spacing: -0.03em;
  margin-bottom: 1.5rem;
}

.banner h1 > span {
  color: var(--color-primary)
}

.banner p {
  font-size: 21px;
}

.home-grid {
  display: grid;
  padding: 5%;
  grid-template-columns: auto 300px;
  grid-column-gap: 2rem;
  column-gap: 2rem; 
  max-width: 100rem;
  margin: 0 auto;
}

.articles {
  background-color: var(--color-lightgrey);
}

@media (max-width: 85rem) {
  .home-grid {
    grid-template-columns: auto;
  }
  .articles-container {
    order: 2;
  }
  
}

@media (max-width: 50rem) {
  .banner h1 {
    letter-spacing: -.03em;
    font-size: 2rem;
  }
}

@media (max-width: 20rem) {
  .banner {
    height: auto;
  }
}
.single-article {
  background-color: white;
}

.full-article {
  padding: 3em;
}

.full-article > img {
  display: block;
  width: 90%;
  height: 350px;
  object-fit: cover;
  margin: 0 auto;
  margin-bottom: 2em;
  border-radius: 0.5rem;
}

.full-article-text {
  max-width: 35rem;
  margin: 0 auto;
}

.full-article-text h1 {
  font-size: 58px;
  font-weight: bold;
  margin-bottom: .2em;
}

.full-article-text time {
  display: block;
  margin-bottom: 1rem;
  color: var(--color-grey)
}

.full-article-text p {
  margin-bottom: 1.5em;
}

.full-article-text  h2 {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: .5em;
}

.full-article .tags {
  display: flex;
}

.full-article .tags > time:first-child {
  margin-right: 1em;
  position: relative;
}

.full-article .tags > time:first-child::after {
  content: "";
  position: absolute;
  height: 75%;
  top: 50%;
  width: 1px;
  right: -0.5rem;
  background-color: var(--color-grey);
  transform: translate(-50%, -50%);
}

@media (max-width: 85rem) {
  .single-article {
    order: 2;
  }
}

@media (max-width: 60rem) {
  .full-article {
    padding: 0;
  }
  .full-article > img {
    width: 100%;
  }
  .full-article-text {
    padding: 1em;
  }
}

@media (max-width: 50rem) {
  .full-article-text h1 {
    font-size: 2rem;
  }
}
